"use client";
import { useEffect, useState } from "react";
import axios from "axios";
import Link from "next/link";

export default function Home() {
  const [question, setQuestion] = useState("");
  const [options, setOptions] = useState(["", ""]);
  const [polls, setPolls] = useState<any[]>([]);

  const fetchPolls = async () => {
    const res = await axios.get("http://localhost:4000/api/polls");
    setPolls(res.data);
  };

  useEffect(() => {
    fetchPolls();
  }, []);

  const createPoll = async () => {
    await axios.post("http://localhost:4000/api/polls", {
      question,
      options,
    });
    setQuestion("");
    setOptions(["", ""]);
    fetchPolls();
  };

  return (
    <main className="p-4">
      <h1>Crear Encuesta</h1>
      <input value={question} onChange={e => setQuestion(e.target.value)} placeholder="Pregunta" />
      {options.map((opt, idx) => (
        <input key={idx} value={opt} onChange={e => {
          const newOpts = [...options];
          newOpts[idx] = e.target.value;
          setOptions(newOpts);
        }} placeholder={`OpciÃ³n ${idx + 1}`} />
      ))}
      <button onClick={createPoll}>Crear</button>

      <h2>Encuestas</h2>
      <ul>
        {polls.map(p => (
          <li key={p.id}><Link href={`/${p.id}`}>{p.question}</Link></li>
        ))}
      </ul>
    </main>
  );
}
